<template>
  <div>
    <table class="table table-hover" style="table-layout: fixed" >
      <thead>
        <tr> 
          <th scope="col">그룹 이름</th>
          <th scope="col">역할</th>
          <th scope="col">공개여부</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="content " colspan="3" style="padding-left: 1em; text-align: left;">
            <RouterLink to="/circlemaker" style="text-decoration:none;color: rgb(6,90,92);font-weight: 500;">
            <img src="../assets/icones/plus-circle-fill.svg" style="width:2em;" alt="join a new circle" class="aligncenter text-success filter-green" >&nbsp;&nbsp;새로운 그룹 만들기
            </RouterLink>
          </td>
        </tr>
        <tr v-for="(list, i) in state.lists" :key="(list, i)">
          <td class="content">
            <RouterLink :to="{name: 'mycircleread', params:{id:list.circleid}}" style="text-decoration:none;color: rgb(6,90,92);font-weight:500;">
              {{ list.name }}
            </RouterLink>
          </td>
          <td v-if="list.roleSet.includes('ROLE_FOUNDER')">그룹 리더</td> 
          <td v-else>회원</td>
          <td>
            <div class="form-check form-switch  checkbox-in-the-middle" >
              <input v-if="!list.privid" class="form-check-input text-end" type="checkbox" role="switch" id="flexSwitchCheckDefault" @change="sendValues(list.circleid, list.privid)" >
              <input v-else class="form-check-input text-end" type="checkbox" role="switch" id="flexSwitchCheckChecked" checked @change="sendValues(list.circleid, list.privid)">
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
  import { onMounted } from 'vue'
  import { reactive,ref } from '@vue/reactivity'
  import axios from 'axios';
  export default {
  setup() {
    onMounted(() => { circleListbyUserHandler(); });
    const state = reactive({
        lists: "",
        email: sessionStorage.getItem("email"),
        token: sessionStorage.getItem("TOKEN"),
    });
    const values = reactive({
      circleid:'',
      privid:''
    })
    const sendValues = (circleid, privid)=>{
      values.circleid = circleid
      values.privid = privid
      changeCmlOption()
    }
    const circleListbyUserHandler = async () => {
      console.log("circleListbyUserHandler launch");
      const url = "http://43.201.60.125/api/circlelistbyuser";
      const headers = {
          "Content-Type": "application/json",
          "Authorization": state.token,
          "token": state.token,
      };
      const body = { user: state.email };
      await axios.post(url, body, { headers }).then(function (res) {
          if (res.data != null) {
              state.lists = res.data;
          }
          else {
              alert("bad.");
          }
      });
    };
    const changeCmlOption = async () =>{
      const url = "http://43.201.60.125/api/changeCmlOption";
        const headers = {
            "Content-Type": "application/json",
            "Authorization": state.token,
            "token": state.token };
        const body = { user: state.email, circleid: values.circleid, privid: !values.privid };
        const response = await axios.post(url, body, {headers})
        if(response.status ==200){
        } else{
          alert('fail')
        }
    }
    return {state, circleListbyUserHandler,changeCmlOption,sendValues,values};
  },
}
</script>
  <style scoped>
.filter-green{
filter: invert(26%) sepia(23%) saturate(2821%) hue-rotate(143deg) brightness(90%) contrast(95%);
    }
     .checkbox-in-the-middle{
  padding-left: 7em;
 }
    .content{
      width: 10px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      word-break: break-all;
    }
  </style>